@import "../../scss/SassVariables.scss";

.reviewContainer {
  width: 100%;
  height: 100%;
}

.addReview {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  margin-bottom: 1rem;
}

.revInput {
  border-radius: 10px;
  height: 4.5rem;
  width: 100%;
}

.sendBtn {
  position: absolute;
  top: 2;
  right: 0;
  bottom: -1;
  margin-right: 1rem;
}
.reviewContainerCards {
  width: 100%;
  height: 90%;
  overflow-y: scroll;
}
